import { useState } from 'react';
import YouTube from 'react-youtube';

const VideoSection = () => {
  const [showVideo, setShowVideo] = useState(false);
  
  // YouTube vide belltsai
  const opts = {
    height: '100%',
    width: '100%',
    playerVars: {
      autoplay: 1,
      modestbranding: 1,
      rel: 0
    },
  };

  // Cserld le ezt a YouTube vide ID-vel (a YouTube URL-bl)
  const videoId = "a4BdlUxAT8g";

  return (
    <section className="mt-20 text-center fade-in">
      <h2 className="text-4xl font-bold text-red-500 mb-8">Vide Elzetes</h2>
      <div className="bg-gray-900 p-8 rounded-lg border border-red-800 max-w-4xl mx-auto">
        <div className="aspect-video bg-gray-800 rounded-lg flex items-center justify-center mb-6 overflow-hidden">
          {showVideo ? (
            <YouTube
              videoId={videoId}
              opts={opts}
              className="w-full h-full"
            />
          ) : (
            <div className="text-center">
              <svg className="w-20 h-20 text-red-500 mx-auto mb-4" fill="currentColor" viewBox="0 0 20 20">
                <path fillRule="evenodd" d="M10 18a8 8 0 100-16 8 8 0 000 16zM9.555 7.168A1 1 0 008 8v4a1 1 0 001.555.832l3-2a1 1 0 000-1.664l-3-2z" clipRule="evenodd" />
              </svg>
              <p className="text-white text-xl">Gameplay Vide</p>
              <p className="text-gray-400">Magyar szinkronnal s feliratokkal</p>
            </div>
          )}
        </div>
        {!showVideo && (
          <button 
            onClick={() => setShowVideo(true)}
            className="bg-red-600 hover:bg-red-700 text-white font-bold py-3 px-8 rounded-lg transform hover:scale-105 transition-all duration-300"
          >
            Vide Lejtszsa
          </button>
        )}
      </div>
    </section>
  );
};

export default VideoSection;